body {
  margin: 0;
  padding: 0;
}

.container {
  width: 350px;
  height: 350px;
  flex-wrap: wrap;
  margin: 50% auto;
  display: flex;
  position: relative;
}

.backdrop {
  width: 100%;
  height: 100%;
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
  background: rgba(255, 255, 255, .37);
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 4px 30px rgba(0, 0, 0, .1);
}

.hidden {
  display: none;
}

.bunner {
  width: 70%;
  height: 40%;
  background-color: #fff;
  border-radius: 15px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-size: 30px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 20px;
}

.box {
  width: 33.3333%;
  height: 33.3333%;
  outline: 1px solid #000;
  justify-content: center;
  align-items: center;
  font-size: 100px;
  display: flex;
}

.restart {
  width: 40%;
  height: 20%;
  cursor: pointer;
  font-size: 20px;
}

